.table-container {
    z-index: 0;
    width: 100%;
    height: 100%;
}
table {
    border-collapse: collapse;
    width: 100%;
    background-color:rgba(173,250,191,0.5);
}
table tr th {
    width: 100px;
    height: 30px;
    border: 1px solid grey;
}

table tr td {
    width: 100px;
    height: 30px;
    border: 1px solid grey;
}

.col_without-item {
    display: none;
}
.col_min {
    width: 10px !important;
}
.bold {
 font-weight: 600;
}
.left {
    text-align: left;
}
.right {
    text-align: right;
}

.loader-wrapper {
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: white;
    z-index: 15;
}
.loader-wrapper .preloader {
    margin: 20% auto;
    width: 80px;
}

@media screen and (max-width: 800px) {
    .loader-wrapper .preloader {
        margin: 50% auto;
    }
}

