
.loader-wrapper {
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: white;
    z-index: 15;
}
.loader-wrapper .preloader {
    margin: 20% auto;
    width: 80px;
}

@media screen and (max-width: 800px) {
    .loader-wrapper .preloader {
        margin: 50% auto;
    }
}
